<template>
  <div >
   <page-header v-on:changeNum="changeNum" @changeItem='changeItem'/>
    <!-- <el-carousel class="home_contant" arrow='never' indicator-position="none">
      <el-carousel-item v-for="item in 1" :key="item">
        <h3>{{ item }}</h3>
      </el-carousel-item>
    </el-carousel> -->
    <img v-if="imageUrl" class="img_header" :src='imageUrl'>
    <!-- <UE :defaultMsg='uetest' :config='myConfig' ref="introduction"></UE> -->
    <!-- <vue-ueditor-wrap readonly="true"  v-model="value" :config="myConfig"></vue-ueditor-wrap> -->
    <div class="contant_under"><content class="content" v-html='html' /></div>
  </div>
</template>

<script>
import Service from '@/services/http'
import PageHeader from '@/layouts/components/header.vue';
//import UE from '@/ue/ue.vue';
//import VueUeditorWrap from 'vue-ueditor-wrap'
export default {
  components: {
      //UE
      PageHeader,
     // VueUeditorWrap
    },
  data() {
  return {
      html: '',
      imageUrl: '',
      solution: [],
      component: [],
      myConfig: { 
        // 编辑器不自动被内容撑高
        autoHeightEnabled: false,
        // 初始容器高度
        initialFrameHeight: 240,
        // 初始容器宽度
        initialFrameWidth: '100%',
        // 上传文件接口（这个地址是我为了方便各位体验文件上传功能搭建的临时接口，请勿在生产环境使用！！！）
        serverUrl: Service.getData(),
        // UEditor 资源文件的存放路径，如果你使用的是 vue-cli 生成的项目，通常不需要设置该选项，vue-ueditor-wrap 会自动处理常见的情况，如果需要特殊配置，参考下方的常见问题2
        UEDITOR_HOME_URL: '/ueditor/'
      }
    }
  },
  async created () {
   if (localStorage.getItem('lang') == 'en-us') {
      this.$i18n.locale = 'en-us'
   } else if (localStorage.getItem('lang') == 'zh-cn') {
      this.$i18n.locale = 'zh-cn'
   } else {
     this.$i18n.locale = 'zh-cn'
    }
    let res = await Service.getData();
    res = JSON.parse(res.data)
    res.Solution.menu.map((item) => {
        if (localStorage.getItem('lang') == 'en-us') {
          if (item.language == 'English') {
            this.solution.push(item)
          }
        } else {
          if (item.language == '中文') {
            this.solution.push(item)
          }
        }
      })
      res.Component.menu.map((item) => {
         if (localStorage.getItem('lang') == 'en-us') {
          if (item.language == 'English') {
            this.component.push(item)
          }
        } else {
          if (item.language == '中文') {
            this.component.push(item)
          }
        }
      })
     this.html = this.solution.length > 0 ? this.solution[0].contant : this.component[0].contant
     this.imageUrl = this.solution.length > 0 ? this.solution[0].imageUrl : this.component[0].imageUrl
  },
  methods: {
    changeItem(item) {
      this.html = item.contant
      this.imageUrl = item.imageUrl
    },
    async changeNum () {
      let res = await Service.getData();
      res = JSON.parse(res.data)
      this.html = res.Solution.menu.length > 0 ? res.Solution.menu[0].contant : res.Component.menu[0].contant
      this.imageUrl = res.Solution.menu.length > 0 ? res.Solution.menu[0].imageUrl : res.Component.menu[0].imageUrl
    }
  }
}
</script>
<style scoped>
  .img_header {
    width: 100%;
    height: 500px;
    object-fit: contain;
    margin-bottom: 50px;
    padding: 0 20px;
    margin-top: 14px;
  }
  .contant_under {
    width: 90%;
    margin: 0 auto;
  }
  .content >>> p {
    word-break: break-all;
    width: 100%
  }
  .content >>> h1 {
    word-break: break-all;
    width: 100%;
    font-weight: bold;
  }
  .content >>> h2 {
    word-break: break-all;
    width: 100%;
    font-weight: bold;
  }
  .content >>> h3 {
    word-break: break-all;
    width: 100%;
    font-weight: bold;
  }
  .content >>> h4 {
    word-break: break-all;
    width: 100%;
    font-weight: bold;
  }
 .content >>> h5 {
    word-break: break-all;
    width: 100%;
    font-weight: bold;
  }
  .content >>> h6 {
    word-break: break-all;
    width: 100%;
    font-weight: bold;
  }
  .content >>> img {
    width: 100% 
  }
  .content >>> .hljs-center {
    text-align: center;
  }
  .content >>> .hljs-right {
    text-align: right;
  }
</style>
